/**
 * @explain
 * This storybook decorator is used to add the base sub-headline text style to
 * the story to make it look like an article sub-headline.
 */
import { FC } from 'react';
import { styled } from 'styled-components';

import { tablet, laptop } from '#theme/devices';

const Wrapper = styled.div`
  p {
    padding: 0;
    margin: 0;
    font: ${({ theme }) => theme.textStyle.subheadline.base.mobile};

    @media ${tablet} {
      font: ${({ theme }) => theme.textStyle.subheadline.base.tablet};
    }

    @media ${laptop} {
      font: ${({ theme }) => theme.textStyle.subheadline.base.laptop};
    }
  }
`;

const Headline = (Story: FC) => (
  <Wrapper>
    <Story />
  </Wrapper>
);

export default Headline;
